@import url(https://fonts.googleapis.com/css2?family=Courier+Prime&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.board {
  font-family: "Courier Prime", Courier, monospace;
  background: darkgrey;
  display: inline-block;
  /* width: calc(min(70vh, 70vw));
  height: calc(min(70vh, 70vw)); */
  padding: 5px;
  border-radius: 1%;
}

.cell {
  border-radius: 10%;
  display: inline-block;
  margin: 5px;
  cursor: pointer;
  font-size: calc(min(70vh / 7 - 10px, 70vw / 6 - 10px));
  text-align: center;
  width: calc(min(70vh / 7 - 10px, 70vw / 6 - 10px));
  height: calc(min(70vh / 7 - 10px, 70vw / 6 - 10px));
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background: white;
}

.cell.hidden {
  color: transparent;
}

.reset {
  position: fixed;
  bottom: 1rem;
  background: blue;
  color: white;
  border: none;
  border-radius: 10px;
  padding: 1rem;
  cursor: pointer;
}

.seeme {
  position: absolute;
  top: 1rem;
}

.seeme a {
  color: blue;
}

#root {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

